<!---->

<template> 
    <!---->
    <el-tree v-loading="loading" :data="data" :expand-on-click-node="false" @node-click="handleNodeClick" 
             :highlight-current="selectable">
    </el-tree>
</template>

<script>
    import menuService from '../../../services/sys/menuService'
    import treeUtils from '../../../utils/treeUtils'

    export default{
        props: {
            onNodeClick: Function,
            value: Array,
            req:Function,
            mark:Object   // {typemenu,name:'name'}
        },
        data () {
            return {
                data: this.value,
                loading: false,
                selectable: true, // 
                type:this.type,
            }
        },
        created () {
             //
                function func(arr) {
                    arr.forEach(v=>{
                        if(v.pid != 0){
                            let t = find(arr,'id',v.pid);
                            t&&(t.children?t.children.push(v):t.children=[v])
                        }
                    });
                    return arr.filter(v=>{
                        return v.pid == 0;
                    })
                }
                function find(arr,key,value){
                    for(let i =0;i<arr.length;i++){
                        if(arr[i][key]==value){
                        return arr[i]
                        }
                    }
                    return false;
                }
               
            //  XX_name->label,id>value
            function transfor(arr,name){
                arr.forEach(v=>{
                    v['label']=v[name]
                    v['value']=v['id']
                })
            
               return arr
            }
            this.req({id : 'tree', type :'menu', filter : true , pid :-1 , level:-1, pagesize:-1}).then(ret=>{ 
                let data=transfor(ret.data,this.mark.name)
                this.data=func(data)
                // this.loading = false
                // xmview.setContentLoading(false)
            })
        },
        methods: {
            handleNodeClick (data, node, store) { //
                // console.log(node.data)
                // if (node.data.ended) return
                    //     let currItem = treeUtils.findItem(this.data, node.data, 'value')   // 
                    //     var arr = []
                    //     this.getData({
                    //         pid: data.data.id, 
                    //         level:-1,
                    //     }).then((ret) => {
                    //         var arr = ret.map(v=>{
                    //             v.label = v.name
                    //             v.value = v.id
                    //             v.children = v.ended ? null : [] //nulgl 
                    //             return v
                    //         })
                    //         currItem.children = arr
                    //         this.loading = false
                    //         xmview.setContentLoading(false)
                    //    })

                 this.$emit('onNodeClick', {data, node, store})
                 if(this.onNodeClick) this.onNodeClick(1, data, node, store) 

                //   
                if (data.value == 0) return
                this.selectable = true
            },
            // 
            clearSelected () {
                this.selectable = false
            },

            removeItem (item, parent) {
                // children 
                if (!parent.data.children) {
                    this.data = this.data.filter((curr) => {
                        return curr.value != item.value
                    })
                } else {
                    parent.data.children = parent.data.children.filter((curr) => {
                        return curr.value != item.value
                    })

                    if (parent.data.children.length < 1) parent.data.children = null
                }
                //   
                this.$emit('input', this.data)
            },



            setCurrVal (val) { //
                if (val === this.data) return
                this.data = val
                this.$emit('input', val)
            },
 
        },
    }

      function findItem(arr,key,value){
        arr.forEach(item=>{
            if(item[key] == value ){
                return item
            }
        })
    }
</script>


